.suggestion-list__radio-input {
  opacity: 0;
}

.suggestion-list__text-container {
  display: inline-block;
}

.suggestion-list__radio-input:focus + .suggestion-list__text-container {
  background-color: olive;
}

.words-suggestion__list:focus {
  background-color: red;
}



